import Srand from "seeded-rand";
import { sampleNoise, generateNoiseMap } from "./PerlinNoise.js";
import { rgb, rgba, clamp, mix } from "./Utils.js";

const STREAM_NUMBERS = 834;
const BACKGROUND = [4,13,33]; // HSB: 221,88,13
const LINE_COLOR = [57,141,237,100]; // HSB: 212,76,93,100
const PERLIN_SIZE = 256;
const STROKE_WIDTH = .1;

const TAU = Math.PI * 2.0;

function sampleNoiseMapLinear(map, x, y) {
  x*= PERLIN_SIZE; y*=PERLIN_SIZE;
  x = clamp(x, 0, PERLIN_SIZE-1);
  y = clamp(y, 0, PERLIN_SIZE-1);

  let xi = Math.floor(x);
  let yi = Math.floor(y);
  let xn = Math.min(xi+1,PERLIN_SIZE-1);
  let yn = Math.min(yi+1,PERLIN_SIZE-1);

  let xf = x-xi;
  let yf = y-yi;

  let v1 = map[xi+yi*PERLIN_SIZE];
  let v2 = map[xn+yi*PERLIN_SIZE];
  let v3 = map[xi+yn*PERLIN_SIZE];
  let v4 = map[xn+yn*PERLIN_SIZE];
  return mix(mix(v1,v2,xf),mix(v3,v4,xf),yf);
}

function sampleNoiseMapNearest(map, x, y) {
  x*= PERLIN_SIZE; y*=PERLIN_SIZE;
  x = clamp(Math.floor(x), 0, PERLIN_SIZE-1);
  y = clamp(Math.floor(y), 0, PERLIN_SIZE-1);
  return map[x + y * PERLIN_SIZE];
}

export class AbstractLines {
  constructor (canvas) {
    this.srand = new Srand();
    this.canvas = canvas;
    this.context = canvas.getContext('2d', {alpha: false, desynchronized: true});
    this.context.imageSmoothingEnabled = false;

    // precompute perlin noise
    this.perlinNoise = generateNoiseMap([0,5],[1,3], {w: PERLIN_SIZE, h: PERLIN_SIZE, octaves: 10});

    window.addEventListener("resize", ()=>this.resized());
    this.resized();
    this.lastFrame = 0;
  }

  resized() {
    if (this.h === this.canvas.clientHeight) return;

    const dpr = Math.ceil(window.devicePixelRatio) || 1;
    this.w = this.canvas.clientWidth;
    this.h = this.canvas.clientHeight
    this.canvas.width = this.w * dpr;
    this.canvas.height = this.h * dpr;
    this.context.scale(dpr, dpr);
    this.progress = 0.0;
    this.invalidate();
  }

  invalidate() {
    this.srand.seed(this.srand.seed());
    this.context.strokeStyle = rgba(...LINE_COLOR);
    this.context.lineWidth = STROKE_WIDTH;
    this.context.fillStyle = rgb(...BACKGROUND);
    this.context.fillRect(0, 0, this.w, this.h);
    this.streams = new Array(STREAM_NUMBERS * 2);
    for (let i = 0; i < STREAM_NUMBERS; i++)
      this.invalidateStream(i);
  }

  invalidateStream(i) {
    const x1 = this.srand.inRange(0, this.w/6); // 1/6
    const x2 = this.srand.inRange(this.w/4, this.w*2/6); // 1/4; 2/6
    const x3 = this.srand.inRange(this.w*4/10, this.w*6/10);  // 4/10; 6/10
    const x4 = this.srand.inRange(this.w*7/10, this.w*9/10); // 7/10; 9/10

    const p1 = 11;
    const p2 = 1;
    const p3 = 5;
    const p4 = 2;

    const p = this.srand.inRange(0, p1+p2+p3+p4);

    let x = 0;
    if (p > p1 + p2 + p3) x = x4;
    else if (p > p1 + p2) x = x3;
    else if (p > p1) x = x2;
    else x = x1;

    this.streams[i*2] = x;
    this.streams[i*2+1] = this.srand.inRange(this.h*1.2, this.h*1.5);
  }

  draw(t) {
    if (this.progress > 1800) return;

    let dt = Math.min(16, (t - this.lastFrame))/16; // not speed up when slow fps
    this.lastFrame = t;
    this.progress += dt;

    for(let i = 0; i < STREAM_NUMBERS; i++) {
      let x = this.streams[i*2];
      let y = this.streams[i*2+1];

      if (y > 0) {
        let cosX = Math.cos( TAU * x/this.w);
        let cosY = Math.cos( TAU * y/this.h);
        let sinX = Math.sin( TAU * x/this.w);
        let sinY = Math.sin( TAU * y/this.h);

        /*
        this.velocity=createVector(
          0.4 - 1 * noise( 3 + 2 * cos( TAU * this.positionB.x/width), 2 + 1 * cos( 1 * TAU * this.positionB.y / height)), 
          0.4 - 2 * noise( 3 + 2 * sin( TAU * this.positionB.x/width), 2 + 1 * sin( 1 * TAU * this.positionB.y / height)) );
        */
        let vx = 0.4 - 1 * sampleNoiseMapLinear(this.perlinNoise, mix(cosX*.5+.5,1,1/5), cosY*.5+.5);
        let vy = 0.4 - 2 * sampleNoiseMapLinear(this.perlinNoise, mix(sinX*.5+.5,1,1/5), sinY*.5+.5);

        // this.velocity.mult(4);
        vx *= 4.0*dt; vy *= 4.0*dt;

        // this.velocity.rotate(0.8*noise(1+sin(TAU*this.positionB.y/height)));
        let angle = .8 * sampleNoiseMapLinear(this.perlinNoise, (0.5*sinY+0.5)*2/5, 0);
        let cosa = Math.cos(angle);
        let sina = Math.sin(angle);

        // Apply rotation matrix instead of atan2 as p5 does.
        let rvx = vx * cosa - vy * sina;
        let rvy = vx * sina + vy * cosa;

        this.context.beginPath();
        this.context.moveTo(x, y);
        this.context.lineTo(x + rvx, y + rvy);
        this.context.stroke();
        //this.context.closePath();

        this.streams[i*2] += rvx; this.streams[i*2+1] += rvy;
      } else if (this.srand.random() < .2) {
        this.invalidateStream(i);
        this.streams[i*2+1] = this.srand.inRange(0, this.h);
      }
    }
  }
}

