import OBJECTS from "../../content/objects.json"
import { initializeLottie, destroyLottie } from "./DomController.js"

const OBJECT_CARD = window.document.querySelector("#object-card");
const OBJECT_BTNS = window.document.querySelector("#object-btns");

export class ObjectCard{
  constructor(load_obj=true){
    this.objectCard = {};
    this.objectCard.root = OBJECT_CARD;
    this.objectCard.header = {};
    this.objectCard.header.main = this.objectCard.root.querySelector("header h1 .main");
    this.objectCard.header.extra = this.objectCard.root.querySelector("header h1 .extra");
    this.objectCard.description = {};
    this.objectCard.description.content = this.objectCard.root.querySelector(".description .content");
    this.objectCard.description.contentModal = this.objectCard.root.querySelector(".modal__graph-text .modal__graph-content .modal__graph-body");
    this.objectCard.objectArea = this.objectCard.root.querySelector(".object-area");
    this.objectCard.modelViewer = this.objectCard.root.querySelector(".object-area model-viewer");
    this.objectCard.graphs = {};
    this.objectCard.graphs.graph1 = this.objectCard.root.querySelector(".graphs .graph1 .graph-1-lottie");
    this.objectCard.graphs.graph2 = this.objectCard.root.querySelector(".graphs .graph2 .graph-2-lottie");
    this.objectCard.graphs.graphModal1 = this.objectCard.root.querySelector(".modal__graph1 .modal__graph-content .modal__graph-body .graph-1-lottie");
    this.objectCard.graphs.graphModal2 = this.objectCard.root.querySelector(".modal__graph2 .modal__graph-content .modal__graph-body .graph-2-lottie");
    this.objectCard.location = {};
    this.objectCard.location.floor = this.objectCard.root.querySelector(".decor-top-center img");
    this.objectCard.location.tree = this.objectCard.root.querySelector(".decor-bottom-center img");

    this.objectBtns = OBJECT_BTNS ? OBJECT_BTNS.querySelectorAll(".object-btn") : null;
    this.selectedObj = -1;

    if (load_obj)
      this.loadObject(0);

    if (this.objectBtns)
      for(let i = 0; i < this.objectBtns.length; i++)
        this.objectBtns[i].addEventListener("click", ()=>{this.loadObject(i);});
  }

  loadObject(id){
    if (this.objectBtns){
      for(let i = 0; i < this.objectBtns.length; i++)
        this.objectBtns[i].classList.remove("selected");
      this.objectBtns[id].classList.add("selected");
    }

    if (id == this.selectedObj || id < 0)
      return;
    this.selectedObj = id;

    this.objectCard.root.style.opacity = 0.0;
    setTimeout(()=>{this.setObject()} , 250);
    setTimeout(()=>{this.objectCard.root.style.opacity = 1.0} , 1000);
  }

  setObject(){
    const obj = OBJECTS[this.selectedObj];
    this.objectCard.header.main.innerText = obj.h1;
    this.objectCard.header.extra.innerText = obj.h1_extra;
    this.objectCard.description.content.innerText = obj.description_intro + obj.description_content;
    this.objectCard.description.contentModal.innerText = obj.description_intro + obj.description_content;

    if (!this.objectCard.modelViewer) {
      this.objectCard.modelViewer = document.createElement("model-viewer");
      this.objectCard.modelViewer.setAttribute("src", "");
      this.objectCard.modelViewer.setAttribute("auto-rotate", "auto-rotate");
      this.objectCard.modelViewer.setAttribute("camera-controls", "camera-controls");
      this.objectCard.objectArea.appendChild(this.objectCard.modelViewer);
    }
    this.objectCard.modelViewer.src = `./content/objects/${obj.category}/${obj.model}`;
    this.objectCard.graphs.graph1.alt = obj["graph1-alt"];
    this.objectCard.graphs.graph2.alt = obj["graph2-alt"];
    this.objectCard.graphs.graph1.dataset.lottie = `./content/lottie/${obj.category}/${obj.graph1}.json`;
    this.objectCard.graphs.graph2.dataset.lottie = `./content/lottie/${obj.category}/${obj.graph2}.json`;
    this.objectCard.graphs.graphModal1.alt = obj["graph1-alt"];
    this.objectCard.graphs.graphModal2.alt = obj["graph2-alt"];
    this.objectCard.graphs.graphModal1.dataset.lottie = `./content/lottie/${obj.category}/${obj.graph1}.json`;
    this.objectCard.graphs.graphModal2.dataset.lottie = `./content/lottie/${obj.category}/${obj.graph2}.json`;
    this.objectCard.location.floor.src = `./img/objects/floor${3 - Math.floor(this.selectedObj/3)}.svg`;
    this.objectCard.location.tree.src = `./img/objects/tree${1 + this.selectedObj}.svg`;

    clearTimeout(this.animationDelay);
    let settings = {
      autoplay: false, loop: false,
      rendererSettings: {
        preserveAspectRatio: 'xMidYMid meet'
      }
    }

    let ga1 = initializeLottie(this.objectCard.graphs.graph1, settings);
    let ga2 = initializeLottie(this.objectCard.graphs.graph2, settings);
    let gaModal1 = initializeLottie(this.objectCard.graphs.graphModal1, settings);
    let gaModal2 = initializeLottie(this.objectCard.graphs.graphModal2, settings);

    this.animationDelay = setTimeout(()=>{
      ga1.play(); ga2.play();
      gaModal1.play(); gaModal2.play();
    }, 1100);
  }

  unloadObject(){
    this.selectedObj = -1;
    this.objectCard.objectArea.innerHTML = ``;
    this.objectCard.modelViewer = undefined;
    destroyLottie(this.objectCard.graphs.graph1);
    destroyLottie(this.objectCard.graphs.graph2);
    destroyLottie(this.objectCard.graphs.graphModal1);
    destroyLottie(this.objectCard.graphs.graphModal2);

    this.objectCard.location.floor.src = ``;
    this.objectCard.location.tree.src = ``;
  }
}