import { disableBodyScroll, enableBodyScroll } from "body-scroll-lock";
import { cross, normalize, binSearch } from "./Demo/Utils.js";
import animateScrollTo from 'animated-scroll-to';
import { ObjectCard } from "./Demo/ObjectCard.js";
import { initializeScrollBooster, initializeLotties } from "./Demo/DomController";

import { AbstractLines } from "./Demo/AbstractLines";
import { CanvasWorker } from "./Demo/CanvasWorker";

const LINES_CANVAS = document.getElementById("idcanvas");
let abstractLines = new AbstractLines(LINES_CANVAS);
let abstractLinesWorker = new CanvasWorker((t)=>abstractLines.draw(t));

class Cache {
  constructor() {
    this.invalidateCache();
  }

  invalidateCache() {
    this.windowInnerHeight = window.innerHeight;
    this.windowInnerWidth = window.innerWidth;
  }
  getCache() {
    return {w: this.windowInnerWidth, h: this.windowInnerHeight};
  }
  checkCache(cache) {
    return cache.w === this.windowInnerWidth && cache.h === this.windowInnerHeight;
  }

  /*
  * Gets offsetTop, offsetLeft & offsetHeight, offsetWidth
  * for an element. Getting these elements causes a reflow,
  * so we are caching them, because they're constant often.
  * We assume these values changes only on window resizing.
  * https://gist.github.com/paulirish/5d52fb081b3570c81e3a
  */
  getElOffsetRect(elem) {
    if (!elem._cachedOffsetRect || !elem._cache || !this.checkCache(elem._cache)) {
      let br = elem.getBoundingClientRect();
      elem._cachedOffsetRect = {top: br.top + window.pageYOffset,
                                left: br.left + window.pageXOffset,
                                width: elem.offsetWidth, height: elem.offsetHeight};
      elem._cache = this.getCache();
    }
    return elem._cachedOffsetRect;
  }
}

let cache = new Cache();


// ANIMATION CONTENT

// class: _anim-items - init aimation in html
// class: _active - init aimation in css (styles animation)
// class: _anim-no-repeat - animation does not repeat again until the page reboot (html) (styles animation)
class AnimationContent {
  constructor(cache) {
    this.animItems = [...document.querySelectorAll('._anim-items')];
    this.animItemsRepeat = document.querySelectorAll('._anim-items:not(._anim-no-repeat)');

    this.animStart = 4; // ??
    this.cache = cache;

    this.sortItems();
    if (this.animItems.length > 0)
      window.addEventListener('scroll', ()=>this.scrolled());

    this.scrolled();
  }

  sortItems() {
    this.animItems.forEach((el)=>{
      let o = this.cache.getElOffsetRect(el);
      el._animOffsetY = o.top + Math.min(o.height, this.cache.windowInnerHeight) / this.animStart;
    });
    this.animItems.sort((a, b)=>(a._animOffsetY - b._animOffsetY));
  }

  scrolled() {
    this.animItemsRepeat.forEach(el => el.classList.remove('_active'));
    let firstId = binSearch(this.animItems, item => -(window.pageYOffset + this.cache.windowInnerHeight - item._animOffsetY));
    // Finds the first element that will be shown next by O(log n)

    let cond = item => {
      let o = this.cache.getElOffsetRect(item);
      return window.pageYOffset < o.top + o.height;
    }
    // Find the last element that are on the screen

    for (let id = firstId - 1; id >= 0 && cond(this.animItems[id]); id--)
      this.animItems[id].classList.add('_active');
  }
}

let resized = false;
let flightJustLoaded = false;
let animation = new AnimationContent(cache);
window.addEventListener("resize", ()=>{resized = true; cache.invalidateCache(); animation.sortItems();});

// FLIGHT
const FLIGHT_BLOCKER = document.querySelector(".flight-free-scroll");
const FLIGHT_FRAME = document.querySelector("#flight-controller");
const FLIGHT_BOX = document.querySelector("#flightBox");

FLIGHT_FRAME.onload = function() {
  flightJustLoaded = true;
  function disableFlight() {
    FLIGHT_BOX.classList.remove("active");
    document.querySelector('.top__header').style.display = 'block';
    enableBodyScroll(FLIGHT_FRAME.contentWindow.document.body);
  }
  FLIGHT_FRAME.contentWindow.document.querySelector("#flight-btn-stop").addEventListener("click",()=> {
      if( FLIGHT_BOX.classList.contains("active") ) {
        disableFlight();
        animateScrollTo(window.pageYOffset+200, {cancelOnUserAction: false})
      }
    }
  );

  FLIGHT_FRAME.contentWindow._flightController.gotoObjectsEvent = ()=>{
    disableFlight();
    animateScrollTo(document.getElementById("sectionIdeaObject"), {cancelOnUserAction: false});
  };
  FLIGHT_FRAME.contentWindow._flightController.gotoGameEvent = ()=>{
    disableFlight();
    animateScrollTo(document.querySelector(".section__game"), {cancelOnUserAction: false});
  };
  FLIGHT_FRAME.contentWindow._flightController.gotoVideoEvent = ()=>{
    disableFlight();
    animateScrollTo(document.getElementById("sectionVideo"), {cancelOnUserAction: false});
  };
};

FLIGHT_BLOCKER.addEventListener("click",()=>{
    if( !FLIGHT_BOX.classList.contains("active") ) {
      FLIGHT_BOX.classList.add("active");
      disableBodyScroll(FLIGHT_FRAME.contentWindow.document.body);
      animateScrollTo(FLIGHT_FRAME, {cancelOnUserAction: false}).then(hasScrolledToPosition=>{
        FLIGHT_FRAME.scrollIntoView({block: "start", behavior: "smooth"});
      });
      document.querySelector('.top__header').style.display = 'none';
    }
  }
);

//  ANIMATION 3dImage
const images3d = document.querySelectorAll(".image3d");
let mousePos = {x: null, y: null};
function handleMouseMove(event) {
  var dot, eventDoc, doc, body, pageX, pageY;

  event = event || window.event; // IE-ism

  mousePos = {
    x: event.clientX,
    y: event.clientY
  };
}
window.addEventListener("mousemove", handleMouseMove);
images3d.forEach((el)=>{el.addEventListener("click", clickDiamond)});

function clickDiamond(e) {
  if (e.detail >= 4) {
    e.currentTarget.querySelector(".idea__img-shadow").style.filter = `hue-rotate(${Math.random()*360}deg)`;
  }
}


function isElementVisible(info) {
  return (info.top <= window.pageYOffset + cache.windowInnerHeight &&
    info.top + info.height >= window.pageYOffset);
}

let lastScroll = 0;
let lastMousePos = mousePos;
let anim = function() {
  let scrollChanged = (lastScroll !== window.pageYOffset);
  if (scrollChanged || resized || flightJustLoaded) {
    if (FLIGHT_FRAME.contentWindow._flightWorker)
      FLIGHT_FRAME.contentWindow._flightWorker.setState(isElementVisible(cache.getElOffsetRect(FLIGHT_FRAME)));
    abstractLinesWorker.setState(isElementVisible(cache.getElOffsetRect(LINES_CANVAS)));
    lastScroll = window.pageYOffset;
    resized = false;
  }

  if (mousePos.x !== null && mousePos.y !== null) {
    if (lastMousePos.x !== mousePos.x || lastMousePos.y !== mousePos.y || scrollChanged) {
      for(let i = 0; i < images3d.length; i++) {

        let info = cache.getElOffsetRect(images3d[i]);
        if (!isElementVisible(info)) continue;

        let dx = mousePos.x - (info.left - window.pageXOffset + info.width * .5);
        let dy = mousePos.y - (info.top - window.pageYOffset + info.height * .5);
        let dz = cache.getElOffsetRect(document.body).width * 2;

        let z = normalize([dx,dy,dz]);
        let x = normalize(cross(z,[0,-1,0]));
        let y = cross(z, x);

        //images3d[i].style.transform = `perspective(500px) matrix3d(${x[0]},${y[0]},${z[0]},${0}, ${x[1]},${y[1]},${z[1]},${0}, ${x[2]},${y[2]},${z[2]},${0}, ${0},${0},${0},${1})`;
        images3d[i].style.transform = `perspective(50vmin) matrix3d(${x[0]},${x[1]},${x[2]},${0}, ${y[0]},${y[1]},${y[2]},${0}, ${z[0]},${z[1]},${z[2]},${0}, ${0},${0},${0},${1}) rotate(45deg) scale(${Math.SQRT1_2}) `;
      }
      lastMousePos = mousePos;
    }
  }
  requestAnimationFrame(anim);
}
requestAnimationFrame(anim);

// header color

const header = document.querySelector('.top__header');
const section_present = document.querySelector('.section__present');

window.addEventListener('scroll', (event)=> {
  let windowScrollTop = window.pageYOffset;
  let heightHeader = cache.getElOffsetRect(section_present).height - cache.getElOffsetRect(header).height;

  if ( windowScrollTop > heightHeader ) {
    header.classList.add('header__color');
  }  else {
    header.classList.remove('header__color');
  }
});

// Mob menu
const menuBurgerBtn = document.querySelector('.menu__burger');
const headerNav = document.querySelector('.header__nav');
const menuItems = document.querySelectorAll('.menu-item-link');

menuBurgerBtn.addEventListener('click', ()=> {
  headerNav.classList.toggle('header__nav--active');
  menuBurgerBtn.classList.toggle('menu__burger--active');
});

menuItems.forEach(item => {
  item.addEventListener('click', function() {
    headerNav.classList.remove('header__nav--active');
  menuBurgerBtn.classList.remove('menu__burger--active');
  })
});

document.body.addEventListener('click', (event)=> {
  if(!event.target.closest('.header__nav--active') && !event.target.closest('.top__header')) {
    headerNav.classList.remove('header__nav--active');
    menuBurgerBtn.classList.remove('menu__burger--active');
  }
});




