// Modal objects
// alret for game on mobile
const btnPlayGame = document.querySelector('.game__play-btn');
const alertGame = document.querySelector('.alert__game');

btnPlayGame.addEventListener('click', (e)=> {
  const gl = document.createElement('canvas').getContext('webgl2');
  if (!gl || mobileAndTabletCheck()) {
    e.preventDefault();
    alertGame.classList.add('show-alert');
    setTimeout(() => {
        alertGame.classList.remove('show-alert');
    }, 4000);
  }
});


if (window.matchMedia("(max-width: 800px)").matches) {
  alertGame.addEventListener('click', (e)=> {
    if (e.target.classList.contains('alert__game-close')) {
      alertGame.classList.remove('show-alert');
    }
  });

// Modal objects
  const graphs = document.querySelectorAll('.graphs');
  const graphText = document.querySelector('.description')
  const modals = document.querySelectorAll('.modal__graph');

  // Open graph madals
  graphs.forEach(itemGraph => {
    itemGraph.addEventListener('click', function(event) {
      if (event.target.closest('.graph1')) {
        document.querySelector('.modal__graph1').style.display = 'block';
        document.body.style.overflowY = 'hidden';
      }
      if (event.target.closest('.graph2')) {
         document.querySelector('.modal__graph2').style.display = 'block';
         document.body.style.overflowY = 'hidden';
      }
    });
  });

  // Open text modal
  graphText.addEventListener('click', function() {
    document.querySelector('.modal__graph-text').style.display = 'block';
    document.body.style.overflowY = 'hidden';
  });

  // Close all modals
  modals.forEach(itemModal => {
    itemModal.addEventListener('click', function(event) {
      const modalClose = document.querySelector('.modal__close');
      if (event.target.classList.contains('modal__close')) {
        this.style.display = 'none';
        document.body.style.overflowY = 'scroll';
      }
    });
  });
};

new ObjectCard();

initializeScrollBooster();
initializeLotties();


function setupAnchorLinks(){
  let links = document.querySelectorAll('a[href^="#"], *[data-href^="#"]');
  for (let i = 0; i < links.length; i++) {
    links[i].addEventListener("click", (e)=>{
      e.preventDefault();
      let d = e.target.dataset.href || e.target.getAttribute("href");
      animateScrollTo(document.querySelector(d));
    });
  }
}
setupAnchorLinks();